import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import {
  getKondisiPinjaman,
  GetDataGroup,
  showModal,
} from "../../../../actions/datamaster_action";
import {
  HiidenFiled,
  ReanderField,
} from "../../../../components/helpers/field";
import { NumberOnly, Gramasi } from "../../../../Validasi/normalize";
import Loading from "react-fullscreen-loading";
import ValidasiHutang from "../../../../Validasi/ValidasiHutang";
import { Camera, imgfound } from "../../../../components/helpers/library";
import { setCamera } from "../../../../actions/databarang_action";
import {
  dataURLtoFile,
  resizeFile,
} from "../../../../components/helpers/function";

class ModalTambahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: true,
      loadimage: imgfound,
      ModalWebCam: false,
      isLoading: false,
      harga_jual: true,
      berat_jual: true,
      hargapergram: true,
      ongkos: true,
      total: true,
      gambar: false,
    };
  }
  componentDidMount() {
    this.props.dispatch(getKondisiPinjaman());
    this.props.dispatch(GetDataGroup());
    setTimeout(() => {
      // document.getElementById("nama_barang_hutang").focus();
      this.props.dispatch(setCamera(false));
    }, 100);
  }

  async handleTakePhoto(url) {
    try {
      const file = dataURLtoFile(url);
      const res = await resizeFile(file);
      // console.log(res);
      this.props.dispatch(setCamera(res));
      this.props.change("foto", res);

      document.getElementById("nama_barang_hutang").focus()
    } catch (err) {
      // console.log(err);
    }
  }
  showModalWebcam() {
    this.props.dispatch(setCamera(false));
    this.props.dispatch(showModal());
    this.setState({ ModalWebCam: true });
  }
  loadcamera() {
    return (
      <Camera
        onTakePhoto={(dataUri) => {
          this.handleTakePhoto(dataUri);
        }}
      />
    );
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-lg-12 text-center">
            <Field
              readOnly={true}
              name="foto"
              type="hidden"
              component={HiidenFiled}
            />
            {this.state.ModalWebCam === true ? (
              this.props.setCamera === false ? (
                <div className="container">{this.loadcamera()}</div>
              ) : (
                <>
                  <img
                    src={
                      this.props.foto === undefined
                        ? this.props.setCamera === false
                          ? imgfound
                          : this.props.setCamera
                        : this.props.setCamera
                        ? this.props.setCamera
                        : this.props.foto
                    }
                    alt="img"
                    height="245px"
                    width="355px"
                  />
                </>
              )
            ) : (
              <>
                <img
                  src={
                    this.props.foto === undefined
                      ? this.props.setCamera === false
                        ? imgfound
                        : this.props.setCamera
                      : this.props.setCamera
                      ? this.props.setCamera
                      : this.props.foto
                  }
                  alt="img"
                  height="245px"
                  width="355px"
                />
              </>
            )}
          </div>
          <div className="col-lg-12 text-center">
            <br></br>
          </div>
          <div className="col-lg-12 text-center">
            <button
              type="button"
              className="btn btn-primary"
              onClick={() => this.showModalWebcam()}
            >
              {" "}
              WebCam{" "}
            </button>
          </div>
          <div className="col-lg-12 text-center">
            <br></br>
          </div>

          <div className="col-4">
            <Field
              id="nama_barang_hutang"
              name="nama_barang"
              component={ReanderField}
              type="text"
              label="Nama Barang"
              placeholder="Masukan Nama Barang"
            />
          </div>
          {/* <div className="col-4">
          <Field
              label="Kondisi Barang"
              name="kondisi_barang"
              options={this.props.datakondisi
                .map((list) => {
                  let data = {
                    value: list.kondisi_barang,
                    name: list.kondisi_barang,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Kondisi Barang"}
              component={ReanderSelect}
            />
          </div> */}
          {/* <div className="col-4">
          <Field
              label="Kategori"
              name="kategori"
              options={this.props.kategori.filter(
                (list) => list.kode_group !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_group,
                    name: list.kode_group +' - '+ list.nama_group,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Kategori"}
              component={ReanderSelect}
            />
          </div> */}
          <div className="col-4">
            <Field
              name="berat"
              component={ReanderField}
              normalize={Gramasi}
              type="text"
              label="Berat"
              placeholder="Masukan Berat"
            />
          </div>
          <div className="col-4">
            <Field
              name="jumlah"
              defaultValue="1"
              component={ReanderField}
              type="jumlah"
              normalize={NumberOnly}
              label="Jumlah"
              placeholder="Masukan Jumlah"
            />
          </div>
          <div className="col-12 text-right">
            <label>&nbsp;</label>
            <button
              className="btn btn-primary "
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                <>
                  <i className="fas fa-paper-plane"></i> &nbsp; Simpan Data
                </>
              )}
            </button>
          </div>
        </div>
        {this.props.isLoading ? (
          <Loading
            loading
            background="rgba(0, 0, 0, 0.35)"
            loaderColor="rgba(94, 147, 117, 1)"
          />
        ) : null}
      </form>
    );
  }
}

// ModalTambahBarang = reduxForm({
//   form: "ModalTambahBarang",
//   enableReinitialize: true,
//   validate: ValidasiMasterDataHadiah,
// })(ModalTambahBarang);
// export default connect(maptostate, null)(ModalTambahBarang);

ModalTambahBarang = reduxForm({
  form: "ModalTambahBarang",
  enableReinitialize: true,
  validate: ValidasiHutang,
})(ModalTambahBarang);

export default connect((state) => {
  return {
    initialValues: {
      jumlah: "1",
    },
    datakondisi: state.datamaster.getKondisiPinjaman,
    kategori: state.datamaster.GetDataGroup,
    foto: state.databarang.showEditBarang.foto,
    setCamera: state.databarang.setCamera,
  };
})(ModalTambahBarang);
