import * as moment from "moment-timezone";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, formValueSelector, reduxForm, reset } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import { hideModal } from "../../../../actions/datamaster_action";
import { setBarangHutang } from "../../../../actions/hutang_action";
import { InputDate, ReanderField } from "../../../../components/helpers/field";
import { getToday } from "../../../../components/helpers/function";
import ValidasiFormHutang from "../../../../Validasi/Hutang/ValidasiFormHutang";
import { NumberOnly,Gramasi } from "../../../../Validasi/normalize";

import Loading from 'react-fullscreen-loading';
import { ToastNotification } from "../../../../components/helpers/notification";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "kr-KO",
});
class ModalHutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // tgl_hutang : 
      isLoadingBatal: false,
    };
    this.setStartDate = this.setStartDate.bind(this);

  }
  setStartDate(date) {
    this.setState({
      tgl_hutang:  new Date(date),
    });
  }
  componentDidMount() {
    let total_berat = 0;
    let data = JSON.parse(localStorage.getItem("data_barang_hutang_tmp"));
    // console.log(data);
    if (data !== null) {
      data.forEach((item, index) => {
        // console.log(item.total_berat);
        total_berat = total_berat + item.berat;
        this.props.change("berat_emas", parseFloat(total_berat).toFixed(1));
        this.props.change("emasdanplastik", parseFloat(total_berat).toFixed(1));
      });
    }

    setTimeout(() => {
      document.getElementById('emasdanplastik').focus();
    }, 100);
  }
  hitungTotal() {
    this.props.change("bunga_rp", this.props.bunga_rp);
    this.props.change("besar_kembali", this.props.besar_kembali);
    this.props.change("tgl_jth_tempo", this.props.tgl_jatuh_tempo);
  }

  batalHutang() {
    this.setState({
      isLoadingBatal: true,
    });
    setTimeout(() => {
      localStorage.setItem("data_customer_hutang_tmp", JSON.stringify([]));
      localStorage.setItem("data_barang_hutang_tmp", JSON.stringify([]));
      this.props.dispatch(setBarangHutang([]));
      this.props.dispatch(reset("ModalHutang"));
      this.props.dispatch(hideModal());
    }, 300);
  }
  cekBeratEmas(e) {
    let berat_emas = document.getElementById('berat_emas').value;
    if (parseFloat(e) < parseFloat(berat_emas)){
      ToastNotification('info', "Berat Yang Diinputkan Tidak Boleh Lebih Kecil Dari Berat Emas")
      this.props.change('emasdanplastik', '');
      document.getElementById('emasdanplastik').focus();
      return false
    }
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        <div className="row">
          <div className="col-4">
            <Field
              readOnly={true}
              id="berat_emas"
              name="berat_emas"
              component={ReanderField}
              type="text"
              label="Berat Emas"
              placeholder="Masukan Berat Emas"
            />
          </div>
          <div className="col-4">
            {/* <Field
              name="tgl_hutang"
              component={ReanderField}
              type="date"
              label="Tanggal Hutang"
              placeholder="dd-mm-yyyy"
            /> */}
            <Field
              name="tgl_hutang"
              component={InputDate}
              label="Tanggal Hutang"
              type="text"
              selected={this.state.tgl_hutang}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>
          <div className="col-4">
            <Field
              id="emasdanplastik"
              name="emasdanplastik"
              type="text"
              component={ReanderField}
              normalize={Gramasi}
              onBlur={(e) => this.cekBeratEmas(e.target.value)}
              label="Berat Emas + Plastik"
              placeholder="Masukan Berat Emas + Plastik"
            />
          </div>
          <div className="col-4">
            <Field
              name="jml_hutang"
              component={ReanderField}
              type="telp"
              {...currencyMask}
              onChange={this.hitungTotal()}
              label="Jumlah Hutang"
              placeholder="Masukan Jumlah Hutang"
            />
          </div>
          <div className="col-4">
            <Field
              name="bunga"
              component={ReanderField}
              type="text"
              normalize={NumberOnly}
              onChange={this.hitungTotal()}
              label="Bunga / Bulan ( % )"
              placeholder="Masukan Bunga / Bulan ( % )"
            />
          </div>
          <div className="col-4">
            <Field
              name="lama_pinjam"
              component={ReanderField}
              type="text"
              normalize={NumberOnly}
              onChange={this.hitungTotal()}
              label="Lama Pinjam"
              placeholder="Masukan Lama Pinjam"
            />
          </div>
          <div className="col-4">
            <Field
              readOnly={true}
              name="tgl_jth_tempo"
              component={ReanderField}
              type="text"
              label="Tanggal Jatuh Tempo"
              placeholder="Masukan Tanggal Jatuh Tempo"
            />
          </div>
          <div className="col-4">
            <Field
              readOnly={true}
              name="bunga_rp"
              component={ReanderField}
              normalize={NumberOnly}
              type="text"
              label="Bunga Rp"
              placeholder="Masukan Bunga Rp"
            />
          </div>
          <div className="col-4">
            <Field
              readOnly={true}
              name="besar_kembali"
              component={ReanderField}
              type="text"
              label="Besar Kembali"
              placeholder="Masukan Besar Kembali"
            />
          </div>

          <div className="col-8"></div>
          <div className="col-2">
           
          </div>
          <div className="col-2">
            <button
              className="btn btn-primary btn-block"
              disabled={this.props.isLoadingHutang}
              type="submit"
            >
              {this.props.isLoadingHutang ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                <>
                  <i className="fas fa-paper-plane"></i> &nbsp; Simpan Data
                </>
              )}
            </button>
          </div>
        </div>
        {this.props.isLoadingHutang ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

ModalHutang = reduxForm({
  form: "ModalHutang",
  enableReinitialize: true,
  validate: ValidasiFormHutang,
})(ModalHutang);
export default connect((state) => {
  const selector = formValueSelector("ModalHutang"); // <-- same as form name
  const { jml_hutang, bunga, lama_pinjam, tgl_hutang } = selector(
    state,
    "jml_hutang",
    "bunga",
    "lama_pinjam",
    "tgl_hutang"
  );
  let bunga_rp =
    (parseFloat(bunga || 0) / parseFloat(100)) *
    parseFloat(jml_hutang || 0) *
    (parseFloat(lama_pinjam || 0) / parseFloat(30));
  let besar_kembali = parseFloat(jml_hutang || 0) + parseFloat(bunga_rp || 0);
  let tomorrow = new Date(tgl_hutang);
  // console.log(tomorrow);
  let tgl_jatuh_tempo = moment(
    tomorrow.setDate(tomorrow.getDate() + parseInt(lama_pinjam || 0))
  ).format("YYYY-MM-DD");
  return {
    initialValues: {
      tgl_hutang: getToday(),
    },
    bunga_rp: parseFloat(bunga_rp).toLocaleString("kr-KO"),
    besar_kembali: parseFloat(besar_kembali).toLocaleString("kr-KO"),
    tgl_jatuh_tempo: tgl_jatuh_tempo,
    datakondisi: state.datamaster.getDataKondisiBarang,
    kategori: state.datamaster.GetDataGroup,
  };
})(ModalHutang);
