import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import { setBarangHutang } from "../../../actions/hutang_action.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import HeadHutang from "./HeadHutang.jsx";

class FormTransaksiHutang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      columns: [
        {
          dataField: "nama_barang",
          text: "Nama Barang",

          footer:""
        },

        // {
        //   dataField: "kondisi_barang",
        //   text: "Kondisi",
        // },
        // {
        //   dataField: "kategori",
        //   text: "Kategori",
        // },
        {
          dataField: "berat",
          text: "Berat",
          footer: columnData => parseFloat(columnData.reduce((acc, item) => acc + item, 0)).toFixed(1)
        },
        {
          dataField: "jumlah",
          text: "Jumlah",
          formatter: (cell) => {
            return <span>{parseInt(cell).toLocaleString("kr-KO")}</span>;
          },
          footer: columnData => parseInt(columnData.reduce((acc, item) => acc + item, 0)).toLocaleString("kr-KO")
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row, index) => {
             
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    type="button"
                    onClick={() => this.hapusdata(index)}
                    className="btn btn-danger mr-3"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer: ""
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  hapusdata(id) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      // console.log(id);
      if (result.isConfirmed) {
        let data = JSON.parse(localStorage.getItem('data_barang_hutang_tmp'));
        data.splice(id, 1)
        localStorage.setItem('data_barang_hutang_tmp', JSON.stringify(data));
        this.props.dispatch(setBarangHutang(data));
        this.setState({
          berhasil : "ok"
        })
      }
    });
  }
  handleSubmit(data) { }
  
  componentWillUnmount() {
    localStorage.setItem("data_customer_hutang_tmp", JSON.stringify([]));
    localStorage.setItem("data_barang_hutang_tmp", JSON.stringify([]));
  }

  render() {
   
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Hutang</Link>
          </li>
          <li className="breadcrumb-item active">Transaksi Hutang</li>
        </ol>
        <h1 className="page-header">Transaksi Hutang </h1>
        <Panel>
          <PanelHeader>Transaksi Hutang</PanelHeader>
          <br />
          {/* Transaksi Hutang */}
          <div className="container">
            <HeadHutang
              isLoading={this.state.isLoading}
              columns={this.state.columns}
            />
          </div>
          <br />
          {/* End Tambah Transaksi Hutang  */}
        </Panel>
      </div>
    );
  }
}


export default connect(null)(FormTransaksiHutang);
