import moment from "moment";
import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm, reset } from "redux-form";
import { hideModal, showModal } from "../../../actions/datamaster_action";
import { setBarangHutang } from "../../../actions/hutang_action";
import { formatDate } from "../../../components/helpers/function";
import ModalGlobal from "../../../components/helpers/ModalGlobal";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import { Panel } from "../../../components/panel/panel";
import { postData } from "../../../config/axios";
import ValidasiDataUsers from "../../../Validasi/ManageUsers/ValidasiDataUsers";
import ModalDataCustomer from "./Modal/ModalDataCustomer";
import ModalHutang from "./Modal/ModalHutang";
import ModalTambahBarang from "./Modal/ModalTambahBarang";

const maptostate = (state) => {
  return {
    setBarangHutang: state.hutang.setBarangHutang,
    DataSales: state.datamaster.getDataSales,
    hideModal: state.datamaster.modalDialog,
  };
};

class HeadHutang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoadingDataCustomer: false,
      isLoadingBatal: false,
      isLoadingHutang: false,
      FormModal: "",
    };
  }
  componentDidMount() {}

  ShowModalCustomer() {
    this.setState({ FormModal: "ShowModalCustomer" });
    this.props.dispatch(showModal());
  }
  ShowModalBarang() {
    this.props.dispatch(showModal());
    this.setState({ FormModal: "ShowModalBarang" });
  }
  ShowModalHutang() {
    let data_customer = JSON.parse(localStorage.getItem("data_customer_hutang_tmp"));
    let data = JSON.parse(localStorage.getItem("data_barang_hutang_tmp"));
    if (data === null || undefined || data.length === 0 || data_customer === null || undefined || data_customer.length === 0) {
      ToastNotification('info', 'Data Customer / Data Barang Belum Diisi')
      return false
    } else {
      this.props.dispatch(showModal());
      this.setState({ FormModal: "ShowModalHutang" });
    }
  }

  // simpanDataCustomer(data) {
    // this.setState(({
    //   isLoadingDataCustomer :true
    // }))
    // setTimeout(() => {
    //   this.setState(({
    //     isLoadingDataCustomer :false
    //   }))
    //   this.props.dispatch(hideModal());
    //   localStorage.setItem("data_customer_hutang_tmp", JSON.stringify(data));
    // }, 500);
  // }

  handleSubmit(data) {
    let hasil = {
      alamat_customer: data.alamat_customer || "-",
      jenis_pelanggan: data.jenis_pelanggan || "NONMEMBER",
      kode_customer: data.jenis_pelanggan === "NONMEMBER" ? "NONMEMBER" : data.kode_customer,
      kode_sales: data.kode_sales,
      nama_customer: data.nama_customer || "REGULER",
      no_hp: data.no_hp || "-"
    }
    this.setState(({
      isLoadingDataCustomer :true
    }))
    localStorage.setItem("data_customer_hutang_tmp", JSON.stringify(hasil));
    setTimeout(() => {
      this.setState(({
        isLoadingDataCustomer :false
      }))
      this.props.dispatch(hideModal());
    }, 500);
  }
 
  simpanTransaksiHutang(data) {
    // console.log(data);
    // console.log(parseInt(data.jml_hutang));
    let data_customer = JSON.parse(localStorage.getItem("data_customer_hutang_tmp"));
    let data_barang_hutang = JSON.parse(localStorage.getItem("data_barang_hutang_tmp"));

    let detail_barang = [];
    data_barang_hutang.forEach((item, index) => {
      let row = {
        nama_barang: item.nama_barang,
        kondisi: item.kondisi_barang,
        kategori: item.kategori,
        foto: item.foto,
        berat: parseFloat(item.berat),
        jumlah: parseInt(item.jumlah),
      };
      detail_barang.push(row);
    });
    let data_hutang = {
      kode_sales: data_customer.kode_sales,
      nama_customer: data_customer.nama_customer,
      alamat_customer: data_customer.alamat_customer,
      kode_member: data_customer.jenis_pelanggan === "NONMEMBER" ? data_customer.jenis_pelanggan : data_customer.kode_customer,
      no_hp : data_customer.no_hp,
      tgl_hutang : formatDate(moment(new Date(data.tgl_hutang)).format('YYYY-MM-DD')),
      tgl_tempo : data.tgl_jth_tempo,
      jumlah_hutang : parseInt(data.jml_hutang),
      lama_pinjam : parseInt(data.lama_pinjam),
      bunga : parseFloat(data.bunga),
      berat_emas : parseFloat(data.berat_emas),
      berat_bruto: parseFloat(data.emasdanplastik),
      detail_barang : detail_barang
    }

    // console.log(data_hutang);
    this.setState({
      isLoadingHutang: true
    })
    setTimeout(() => {
      postData('hutang/simpan', data_hutang).then((res) => {
        ToastNotification('success', res.data.message)
        this.setState({
          isLoadingHutang: false
        })
        this.props.dispatch(reset('ModalHutang'))
      }).then(() => {
        localStorage.setItem("data_customer_hutang_tmp", JSON.stringify([]));
        localStorage.setItem("data_barang_hutang_tmp", JSON.stringify([]));
        this.props.dispatch(setBarangHutang([]))
        this.setState({
          isLoadingHutang: false
        })
      }).then(() => {
        this.props.dispatch(hideModal())
      })
        .catch((err) => {
          this.setState({
            isLoadingHutang: false
          })
        ToastNotification('info','Transaksi Gagal Disimpan')
      })
    }, 500);
    
  }
  batalHutang() {
    this.setState({
      isLoadingBatal: true,
    });
    setTimeout(() => {
      localStorage.setItem("data_customer_hutang_tmp", JSON.stringify([]));
      localStorage.setItem("data_barang_hutang_tmp", JSON.stringify([]));
      this.props.dispatch(setBarangHutang([]));
      this.props.dispatch(reset("ModalHutang"));
      this.props.dispatch(hideModal());
      this.setState({
        isLoadingBatal: false,
      });
    }, 300);
  }
  simpanBarang(data) {
    let hasil = {
      nama_barang: data.nama_barang || "-",
      kondisi_barang: data.kondisi_barang || "-",
      kategori: data.kategori || "-",
      berat: parseFloat(data.berat) || 0,
      jumlah: parseInt(data.jumlah) || 0,
      foto: data.foto || "-",
    };
    let cek = JSON.parse(localStorage.getItem("data_barang_hutang_tmp")) || [];
    cek.push(hasil);
    document.getElementById('nama_barang_hutang').focus()
    this.props.dispatch(reset('ModalTambahBarang'));
    this.props.dispatch(setBarangHutang(cek));
    localStorage.setItem("data_barang_hutang_tmp", JSON.stringify(cek));
    this.props.dispatch(hideModal())
  }
  render() {
    return (
      <Panel>
        <div className="row">
          <div className="col-12">
            <Tabel
              keyField="nama_barang"
              exportCsv={false}
              data={
                this.props.setBarangHutang.length === 0 ? JSON.parse(localStorage.getItem("data_barang_hutang_tmp")) || []
                : this.props.setBarangHutang || []
              }
              tambahData={true}
              btnText="Data Customer"
              handleClick={() => this.ShowModalCustomer()}
              columns={this.props.columns}
              btnOptional={true}
              namaCustomer={true}
              jenisCustomer={true}
              form="Hutang"
              btnTextOptional="Data Barang"
              handleClickOptional={() => this.ShowModalBarang()}
              btnOptional2={true}
              btnTextOptional2="Data Hutang"
              handleClickOptional2={() => this.ShowModalHutang()}
              empty={true}
              textEmpty="Data Barang Kosong"
            />
          </div>
          
        </div>
        <div className="row">
            <div className="col-6">
            <button
              onClick={() => this.batalHutang()}
              className="btn btn-warning"
              disabled={this.state.isLoadingBatal}
              type="button"
            >
              {this.state.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Reset
                </>
              ) : (
                "Reset"
              )}
            </button>
            </div>
          </div>
        <ModalGlobal
          size={this.state.FormModal === "ShowModalBarang" ? "l" :"xl"}
          title={<TitleModal state={this.state.FormModal} />}
          content={
            this.state.FormModal === "ShowModalCustomer" ? (
              <ModalDataCustomer isLoadingDataCustomer={this.state.isLoadingDataCustomer} onSubmit={(data) => this.handleSubmit(data)} />
            ) : this.state.FormModal === "ShowModalBarang" ? (
              <ModalTambahBarang onSubmit={(data) => this.simpanBarang(data)} />
            ) : (
              <ModalHutang isLoadingHutang={this.state.isLoadingHutang} onSubmit={(data) => this.simpanTransaksiHutang(data)} />
            )
          }
        />
      </Panel>
    );
  }
}

function TitleModal(data, onSubmit) {
  // console.log(state.state);
  if (data.state === "ShowModalCustomer") {
    return "Form Data Customer";
  } else if (data.state === "ShowModalBarang") {
    return "Form Data Barang";
  } else {
    return "Form Data Hutang";
  }
}
HeadHutang = reduxForm({
  form: "HeadHutang",
  enableReinitialize: true,
  validate: ValidasiDataUsers,
})(HeadHutang);
export default connect(maptostate, null)(HeadHutang);
