const ValidasiFormHutang = (value, event) => {
    const errors = {};
    // console.log(event.charCode==13);
    if (!value.tgl_hutang) {
      errors.tgl_hutang = "Tanggal Hutang Tidak Boleh Kosong";
    }
    if (!value.emasdanplastik) {
      errors.emasdanplastik = "Berat Emas + Plastik Tidak Boleh Kosong";
    }
    if (!value.jml_hutang) {
      errors.jml_hutang = "Jumlah Hutang Tidak Boleh Kosong";
    }
    if (!value.bunga) {
      errors.bunga = "Bunga / Bulan Tidak Boleh Kosong";
    }
    if (!value.lama_pinjam) {
      errors.lama_pinjam = "Lama Pinjam Tidak Boleh Kosong";
    }
    return errors;
  };
  
  export default ValidasiFormHutang;
  