const ValidasiHutang = (value, event) => {
    const errors = {};
    // console.log(event.charCode==13);
    if (!value.nama_barang) {
      errors.nama_barang = "Nama Barang Tidak Boleh Kosong";
    }
    if (!value.berat) {
      errors.berat = "Berat Tidak Boleh Kosong";
    }
    if (!value.jumlah) {
      errors.jumlah = "Jumlah Tidak Boleh Kosong";
    }

    return errors;
  };
  
  export default ValidasiHutang;
  